import React, { Component } from 'react';
import {Table, Button, Confirm, Modal} from 'semantic-ui-react'
import EditContractor from './EditContractor.js'
import _ from 'lodash'

class Contractors extends Component {
  state = {
        column: null,
        data: [],
        direction: null,
        delConfirmOpen: false,
        editing: false,
        editingEl: {},
        toDelete: ""
      }
  openDelConfirm = (uuid) => this.setState({ delConfirmOpen: true, toDelete: uuid })
  closeDelConfirm  = () => this.setState({ delConfirmOpen: false, toDelete: "" })
  delete = () => {
    this.closeDelConfirm()
    this.props.deleteContractor(this.state.toDelete)
  }
  componentDidUpdate(prevprops){
      if(prevprops.data !== this.props.data) {         
          this.setState({data: this.props.data})
      }
  }  
  handleSort = clickedColumn => () => {
        const { column, data, direction } = this.state
        if (column !== clickedColumn) {
          this.setState({
            column: clickedColumn,
            data: _.sortBy(data, [clickedColumn]),
            direction: 'ascending',
          })
          return
        }
        this.setState({
          data: data.reverse(),
          direction: direction === 'ascending' ? 'descending' : 'ascending',
        })
      }

  startEditing = (el) => {
    this.setState({editing: true, editingEl: el})
  }
  cancelEditing = () => {
    this.setState({editing: false})
 }  
  render() {
    const { column, data, direction } = this.state
    return (  
        <Table compact padded fixed sortable>
        <Confirm open={this.state.delConfirmOpen} onCancel={this.closeDelConfirm} onConfirm={this.delete} />
        <Table.Header fullWidth>
            <Table.Row>
                <Table.HeaderCell 
                    sorted={column === 'firstName' ? direction : null}
                    onClick={this.handleSort('firstName')}>First Name</Table.HeaderCell>
                <Table.HeaderCell 
                    sorted={column === 'lastName' ? direction : null}
                    onClick={this.handleSort('lastName')}>Last Name</Table.HeaderCell>
                <Table.HeaderCell 
                    sorted={column === 'company' ? direction : null}
                    onClick={this.handleSort('company')}>Company</Table.HeaderCell>
                <Table.HeaderCell 
                    sorted={column === 'phone' ? direction : null}
                    onClick={this.handleSort('phone')}>Phone</Table.HeaderCell>
                {/* <Table.HeaderCell
                    sorted={column === 'id' ? direction : null}
                    onClick={this.handleSort('id')}>ID</Table.HeaderCell> */}
                <Table.HeaderCell
                    sorted={column === 'notes' ? direction : null}
                    onClick={this.handleSort('notes')}>Notes</Table.HeaderCell> 
                <Table.HeaderCell width="2"></Table.HeaderCell> 
            </Table.Row>
        </Table.Header>
        <Table.Body>
        {data.map((el, i)=>{
            return(
                <Table.Row key={i}>    
                <Table.Cell>{el.firstName}</Table.Cell>
                <Table.Cell>{el.lastName}</Table.Cell>
                <Table.Cell>{el.company}</Table.Cell>
                <Table.Cell>{el.phone}</Table.Cell>
                {/* <Table.Cell>{el.id}</Table.Cell> */}
                <Table.Cell>{el.notes}</Table.Cell>
                <Table.Cell textAlign="center" style={{display: "flex", justifyContent: "space-around"}}>
                <Button size='mini' compact onClick={()=>this.startEditing(el)} >Edit</Button>
                <Button size='mini' compact negative onClick={()=>this.openDelConfirm(el.uuid)}>Delete</Button>  
                </Table.Cell>
              </Table.Row>
            )       
        })}
        <Modal onClose={this.cancelEditing} size="large" open={this.state.editing}>
                   <EditContractor el={this.state.editingEl} editFormData={this.props.editFormData} />
                   <Modal.Actions>
                        <Button negative onClick={this.cancelEditing}>Cancel</Button>
                        <Button positive icon='checkmark' labelPosition='right' content='Change' onClick={()=> {
                            this.props.doneEditing()
                            this.cancelEditing()
                        }}/>
                     </Modal.Actions>
        </Modal>  
        </Table.Body>
                        <Table.Footer>
                    <Table.Row>
                        <Table.HeaderCell colSpan='6'>
                            {this.props.msg || "Found: " + this.state.data.length}
                        </Table.HeaderCell>
                    </Table.Row>
                    </Table.Footer>
        </Table>
    );
  }
}

export default Contractors;