import React, { Component } from 'react';
import Search from './components/Search'
import Contractors from './components/Contractors'
import AddContractor from './components/AddContractor'
import {Container, Divider, Header, Modal, Button, Segment} from 'semantic-ui-react'
import firebase from './firebase.js';
const db= firebase.database().ref('contractors');



class App extends Component {
  
  state = {data: [], filtered: [], filter: "", editing: false, editFormData:{}, addFormData: {}, modalOpen: false, fetching: true}

  addData = (data) => {
    db.push(data);
    this.setState({addFormData:{}})
  }

  editFormData = (data) =>{
    this.setState({editFormData: data})
  }

  doneEditing = () => {
    const item = firebase.database().ref(`/contractors/${this.state.editFormData.uuid}`);
    item.update(this.state.editFormData)
    this.setState({editFormData: {}})
  }
   
  deleteContractor = (uuid) => {
    const item = firebase.database().ref(`/contractors/${uuid}`);
    item.remove();
  }
  
  componentDidMount() {

    db.on('value', snap => {
      
      let items = snap.val();
      let newState = []
      for (let item in items) {
        newState.push({
          uuid: item,
          firstName: items[item].firstName,
          lastName: items[item].lastName,
          company: items[item].company,
          id: items[item].id,
          phone: items[item].phone,
          notes: items[item].notes
        });
      }
  
      this.setState({ 
        data: newState
      }, ()=>{
        this.setState({filtered: this.state.data, fetching: false})
      });
    });
    
    
  }

  filterContractors = (val) =>{
    this.setState({filter: val})
    let arr = [...this.state.data]
    this.setState({filtered: []})
    let filtered = arr.filter((el, i)=>{
      for(let key in el) {
       el[key] += ""
       if (key !== "uuid" &&  el[key] && el[key].toLowerCase().match(val.toLowerCase())) {
         return el;
       }     
      }
      return null;
    })
      this.setState({filtered})   
  }
  

  addFormData =(data) => {
    this.setState({addFormData: data})
  }
  submitFormData = () => {
    this.addData(this.state.addFormData)
    this.handleClose()
  }
  
  handleOpen = () => this.setState({ modalOpen: true })

  handleClose = () => this.setState({ modalOpen: false })

  render() {
    let msg = ""
    if(this.state.filtered.length === 0 && !this.state.fetching) msg = "Nothing found..."
    if(this.state.fetching) msg = "Fetching data..."
 
    return (
      <Container>
        <Header content='ðŸ‘· Contractor List' style={{
            fontSize: '4em',
            fontWeight: 'normal',
            marginBottom: 0,
            paddingTop: '1em',
      }}/> 
        <Divider />
        <div className = "top-bar">
        <Search filterContractors={this.filterContractors}/>
        <Modal size="large" open={this.state.modalOpen}
          onClose={this.handleClose} trigger={<Button primary onClick={this.handleOpen}>Add</Button>} >
            <AddContractor addFormData={this.addFormData} addData={this.addData}/>
            <Modal.Actions>
              <Button negative onClick={this.handleClose}>Cancel</Button>
              <Button positive icon='checkmark' labelPosition='right' content='Add' onClick={this.submitFormData}/>
          </Modal.Actions>
        </Modal>
        </div>     
        <Contractors msg={msg}data={this.state.filtered} deleteContractor={this.deleteContractor} editFormData={this.editFormData}  doneEditing={this.doneEditing}/>
      
        <Segment vertical style={{ padding: '1em 0em' }}></Segment>
      </Container>
       
    );
  }
}

export default App;
