import React, { Component } from 'react';
import {Form, Input, TextArea} from 'semantic-ui-react'




class AddContractor extends Component {
  state={data: {firstName: "", lastName: "", company: "", phone: "", id: "", notes: ""}}

  onChange = e => {
    let data = {...this.state.data, [e.target.name]: e.target.value }
    this.setState({data})
    this.props.addFormData(data)
  };

  onSubmit = () => {
    let data = this.state.data
    this.props.addData(Object.assign({}, data))
    for (var key in data) {
      data[key] = ""
    }
    this.setState({data})
  }

  render() {
    const { data} = this.state;
    return (
      <Form style= {{padding: "2em"}} onSubmit={this.onSubmit}>
      <Form.Group widths='equal'>
        <Form.Field
          id='form-input-control-first-name'
          control={Input}
          label='First name'
          name="firstName"
          placeholder='First name'
          value={data.firstName}
          onChange={this.onChange}
        />
        <Form.Field
          id='form-input-control-last-name'
          control={Input}
          name="lastName"
          label='Last name'
          placeholder='Last name'
          value={data.lastName}
          onChange={this.onChange}
        />
         <Form.Field
          id='form-input-control-company'
          control={Input}
          name="company"
          label='Company'
          placeholder='Company'
          value={data.company}
          onChange={this.onChange}
        />
         <Form.Field
          id='form-input-control-phone'
          control={Input}
          name="phone"
          label='Phone'
          placeholder='Phone'
          value={data.phone}
          onChange={this.onChange}
        />
        <Form.Field
          id='form-input-control-id'
          control={Input}
          name="id"
          label='ID'
          placeholder='ID'
          value={data.id}
          onChange={this.onChange}
        />
      </Form.Group>
      <Form.Field
        id='form-textarea-control-notes'
        control={TextArea}
        name="notes"
        label='Notes'
        placeholder='Notes'
        value={data.notes}
        onChange={this.onChange}
      />
 
    </Form>
    );
  }
}

export default AddContractor;